@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald Regular"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: local("Oswald Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  padding: 10px 20px 17px 20px;
}

.site-list__item {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}

.promo {
  margin: 0;
  padding: 0 20px;
  background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/index-background-cat.jpg");
  background-repeat: no-repeat;
  background-size: cover, contain;
}

.promo__description {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
}

.promo__slogan {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
}

.promo__image {
  display: block;
  width: 280px;
  height: 271px;
}

.promo__link {
  display: block;
  width: 100%;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  background: #68b738;
}

.product-lines__wrapper {
  margin: 20px;
  background-color: #f2f2f2;
  padding-left: 20px;
  background-image: url("../../img/cat-icon-1.svg");
  background-repeat: no-repeat;
  background-position: top 24px left 20px;
}

.product-lines__wrapper:last-child {
  background-image: url("../../img/cat-icon-2.svg");
}

.product-lines__title {
  margin: 0;
  padding: 0;
  padding-top: 30px;
  padding-left: 91px;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
}

.product-lines__description {
  margin: 0;
  padding: 0;
  padding-top: 29px;
  padding-bottom: 23px;
}

.product-lines__link {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  padding-top: 19px;
  padding-bottom: 22px;
  border-top: 1px solid #d9d9d9;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}

.product-lines__link::after {
  content: "";
  width: 24px;
  height: 12px;
  background-image: url("../../img/arrow.svg");
  background-repeat: no-repeat;
  position: absolute;
  left: 100px;
  top: 21px;
}

.product-information {
  margin: 0 20px 49px 20px;
}

.product-information__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}

.product-information__item-first {
  position: relative;
}

.product-information__item-first::before {
  content: "";
  width: 60px;
  height: 60px;
  background-image: url("../../img/green-box-1.svg");
  background-repeat: no-repeat;
  position: absolute;
}

.product-information__item-second {
  position: relative;
}

.product-information__item-second::before {
  content: "";
  width: 60px;
  height: 60px;
  background-image: url("../../img/green-box-2.svg");
  background-repeat: no-repeat;
  position: absolute;
}

.product-information__item-third {
  position: relative;
}

.product-information__item-third::before {
  content: "";
  width: 60px;
  height: 60px;
  background-image: url("../../img/green-box-3.svg");
  background-repeat: no-repeat;
  position: absolute;
}

.product-information__item-fourth {
  position: relative;
}

.product-information__item-fourth::before {
  content: "";
  width: 60px;
  height: 60px;
  background-image: url("../../img/green-box-4.svg");
  background-repeat: no-repeat;
  position: absolute;
}

.example {
  margin: 0;
  padding: 25px 20px 40px 20px;
  background: #eaeaea;
}

.example__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}

.example__cell-inside {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
}

.example__cell-border {
  font-size: 12px;
}

.example__result {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}

.contacts {
  margin: 0;
  padding: 26px 20px;
}

.contacts__advertisement {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
